/**
 * Site header
 */
.site-header {
  background-color:$background-color-dark;
  color:$text-color-light;
  height: 64px;
  padding:0 24px;
  z-index:100;
  text-transform: uppercase;
  letter-spacing: 1px;

  // Positioning context for the mobile navigation icon
  position: fixed;
  top:0;
  left:0;
  right:0;
}

.site-title {
  @include relative-font-size(1);
  line-height: 64px;
  margin-bottom: 0;
  float: left;

  &,
  &:visited {
    color: $text-color-light;
  }

  &:hover {
    color: $brand-color;
  }
}

.site-nav {
  float: right;
  line-height: 64px;

  .nav-trigger {
      display: none;
  }

  .menu-icon {
    display: none;
  }

  .page-link {
    color: $text-color-light;
    line-height: 64px;

    // Gaps between nav items, but not on the last one
    &:not(:last-child) {
      margin-right: 32px;
    }

    &:hover {
      color: $brand-color;
    }
  }

  @include media-query($nav-min) {
    position: absolute;
    top: 8px;
    right: 8px;
    text-align: right;

    label[for="nav-trigger"] {
      display: block;
      float: right;
      width: 48px;
      height: 48px;
      z-index: 2;
      cursor: pointer;
    }

    .menu-icon {
      display: block;
      float: right;
      width: 48px;
      height: 32px;
      line-height: 0;
      padding-top: 16px;
      text-align: center;

      > svg {
        fill: $text-color-light;
      }
    }

    input ~ .trigger {
      clear: both;
      display: none;
    }

    input:checked ~ .trigger {
      display: block;
      padding-bottom: 5px;
      padding-top: 8px;
      margin-right: -8px;
    }

    .page-link {
      display: block;
      padding: 6px 32px;
      text-align: left;
      line-height: 48px;
      background-color: $background-color-dark;

      &:not(:last-child) {
        margin-right: 0;
      }

      &:hover {
        color:$text-color-dark;
        background-color:$brand-color;
      }
    }
  }
}



/**
 * Site footer
 */
.site-footer {
  padding: $spacing-unit 16px;
  z-index: 99;
  .svg-icon {
    fill:#{$brand-color};
  }
}

.footer-heading {
  @include relative-font-size(1.125);
  margin-bottom: $spacing-unit / 2;
}

.contact-list,
.social-media-list {
  list-style: none;
  margin-left: 0;
}

.footer-col-wrapper {
  @include relative-font-size(0.9375);
  color: $grey-color;
  margin-left: -$spacing-unit / 2;
  @extend %clearfix;
}

.footer-col {
  float: left;
  margin-bottom: $spacing-unit;
  padding-left: $spacing-unit;
  width: -webkit-calc(33% - (#{$spacing-unit}));
  width:         calc(33% - (#{$spacing-unit}));
}

.footer-col-1 {

}

.footer-col-2 {
}

.footer-col-3 {
}

@include media-query($on-laptop) {
  .footer-col-1,
  .footer-col-2 {
    width: -webkit-calc(50% - (#{$spacing-unit} / 2));
    width:         calc(50% - (#{$spacing-unit} / 2));
  }

  .footer-col-3 {
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width:         calc(100% - (#{$spacing-unit} / 2));
  }
}

@include media-query($on-palm) {
  .footer-col {
    float: none;
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width:         calc(100% - (#{$spacing-unit} / 2));
  }
}



/**
 * Page content
 */
.page-content {
  margin-top: 72px;
  margin-bottom: 72px;
  padding: $spacing-unit 0;
  flex: 1;
}

.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  @include relative-font-size(1.75);
}

.post-list {
  margin-left: 0;
  list-style: none;

  > li {
    margin-bottom: $spacing-unit;
  }
}

.post-meta {
  font-size: $small-font-size;
  color: $grey-color;
}

.post-link {
  display: block;
  @include relative-font-size(1.5);
}



/**
 * Posts
 */
.post-header {
  margin-bottom: $spacing-unit;
}

.post-title {
  @include relative-font-size(2);
  line-height: 1;
  font-weight:900;

  @include media-query($on-laptop) {
    @include relative-font-size(1.75);
  }
}

h1 {
  font-weight: 900;
  @include relative-font-size(2);
  text-align: center;
  line-height: 1.2;
}

.post-content {
  margin-bottom: $spacing-unit;

  h2 {
    @include relative-font-size(1.65);

    @include media-query($on-laptop) {
      @include relative-font-size(1.65);
    }
  }

  h3 {
    @include relative-font-size(1.5);

    font-weight: 900;

    @include media-query($on-laptop) {
      @include relative-font-size(1.5);
    }
  }

  h4 {
    @include relative-font-size(1);

    font-style: italic;

    @include media-query($on-laptop) {
      @include relative-font-size(1);
    }
  }
}

/**
 * Side Navigation
 */
.side-nav {
  @media (min-width:1200px) {
    background-color:$background-color-dark;
    position:fixed;
    overflow-y:auto;
    overflow-x:hidden;
    left:0;
    top:64px;
    bottom:0px;
    min-width:256px;
    border-right:1px solid $grey-color-light;
    padding: 8px 0;
    font-size:18px;
    line-height: 1.3;
    z-index:99;

    h3 {
      display:none;
    }

    ul {
      margin: 0;
      padding: 0;
    }
    li {
      list-style-type: none;
      a{
        display: block;
        width: 100%;
        padding: 4px 16px;
        color: $text-color-light-2 !important;
        font-weight: $base-font-weight !important;
      }
      a:hover {
        color:$text-color-dark !important;
        text-decoration:none;
        background:$brand-color;
      }
    }
    li:hover{
      
    }
  }
}

.thumbnails {
  line-height: 0;
  -webkit-column-count: 2;
  -webkit-column-gap:   8px;
  -moz-column-count:    2;
  -moz-column-gap:      8px;
  column-count:         2;
  column-gap:           8px;
 }

.thumbnails-3 {
  -webkit-column-count: 3;
  -webkit-column-gap:   8px;
  -moz-column-count:    3;
  -moz-column-gap:      8px;
  column-count:         3;
  column-gap:           8px;

  @include media-query($on-palm) {
    -webkit-column-count: 2;
    -webkit-column-gap:   8px;
    -moz-column-count:    2;
    -moz-column-gap:      8px;
    column-count:         2;
    column-gap:           8px;
  }
}

.thumb {
  box-sizing: border-box;
  border: 2px solid $grey-color-dark;
  margin:2px;
  width: 100% !important;
  height: auto !important;

  &:hover {
    border: 2px solid $brand-color;
  }
}

a.top {
position: relative;
top:-96px;
display: block;
height: 0;
width:0;
}


.auto-resizable-iframe {
  margin: 0px auto;
}

.auto-resizable-iframe > div {
  position: relative;
  padding-bottom: 56.25%;
  height: 0px;
}

.auto-resizable-iframe iframe {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}