/**
 * Syntax highlighting styles
 */

$oceanic-00: #1B2B34; // darkest
$oceanic-01: #343D46;
$oceanic-02: #4F5B66;
$oceanic-03: #65737E;
$oceanic-04: #A7ADBA;
$oceanic-05: #C0C5CE;
$oceanic-06: #CDD3DE;
$oceanic-07: #D8DEE9; // lightest
$oceanic-08: #EC5f67; // red
$oceanic-09: #F99157; // orange
$oceanic-10: #FAC863; // yellow
$oceanic-11: #99C794; // green
$oceanic-12: #5FB3B3; // aqua
$oceanic-13: #6699CC; // blue
$oceanic-14: #C594C5; // purple
$oceanic-15: #AB7967; // brown

.highlight {
  @extend %vertical-rhythm;

  .highlighter-rouge & {
  }

  .c     { color: $oceanic-11; font-style: italic } // Comment
  .err   { color: $oceanic-00; background-color: $oceanic-08 } // Error
  .k     { font-weight: bold } // Keyword
  .o     { font-weight: bold } // Operator
  .cm    { color: $oceanic-11; font-style: italic } // Comment.Multiline
  .cp    { color: $oceanic-11; font-weight: bold } // Comment.Preproc
  .c1    { color: $oceanic-11; font-style: italic } // Comment.Single
  .cs    { color: $oceanic-11; font-weight: bold; font-style: italic } // Comment.Special
  .gd    { color: $oceanic-00; background-color: $oceanic-05 } // Generic.Deleted
  .gd .x { color: $oceanic-00; background-color: $oceanic-06 } // Generic.Deleted.Specific
  .ge    { font-style: italic } // Generic.Emph
  .gr    { color: $oceanic-08 } // Generic.Error
  .gh    { color: $oceanic-07 } // Generic.Heading
  .gi    { color: $oceanic-00; background-color: $oceanic-05 } // Generic.Inserted
  .gi .x { color: $oceanic-00; background-color: $oceanic-06 } // Generic.Inserted.Specific
  .go    { color: $oceanic-09 } // Generic.Output
  .gp    { color: $oceanic-10 } // Generic.Prompt
  .gs    { font-weight: bold } // Generic.Strong
  .gu    { color: $oceanic-01 } // Generic.Subheading
  .gt    { color: $oceanic-12 } // Generic.Traceback
  .kc    { font-weight: bold } // Keyword.Constant
  .kd    { font-weight: bold } // Keyword.Declaration
  .kp    { font-weight: bold } // Keyword.Pseudo
  .kr    { font-weight: bold } // Keyword.Reserved
  .kt    { color: $oceanic-12; font-weight: bold } // Keyword.Type
  .m     { color: $oceanic-09 } // Literal.Number
  .s     { color: $oceanic-09 } // Literal.String
  .na    { color: $oceanic-12 } // Name.Attribute
  .nb    { color: $oceanic-12 } // Name.Builtin
  .nc    { color: $oceanic-12; font-weight: bold } // Name.Class
  .no    { color: $oceanic-12 } // Name.Constant
  .ni    { color: $oceanic-12 } // Name.Entity
  .ne    { color: $oceanic-12; font-weight: bold } // Name.Exception
  .nf    { color: $oceanic-09; font-weight: bold } // Name.Function
  .nn    { color: $oceanic-05 } // Name.Namespace
  .nt    { color: $oceanic-06 } // Name.Tag
  .nv    { color: $oceanic-12 } // Name.Variable
  .ow    { font-weight: bold } // Operator.Word
  .w     { color: $oceanic-05 } // Text.Whitespace
  .mf    { color: $oceanic-14 } // Literal.Number.Float
  .mh    { color: $oceanic-14 } // Literal.Number.Hex
  .mi    { color: $oceanic-14 } // Literal.Number.Integer
  .mo    { color: $oceanic-14 } // Literal.Number.Oct
  .sb    { color: $oceanic-08 } // Literal.String.Backtick
  .sc    { color: $oceanic-08 } // Literal.String.Char
  .sd    { color: $oceanic-08 } // Literal.String.Doc
  .s2    { color: $oceanic-09 } // Literal.String.Double
  .se    { color: $oceanic-08 } // Literal.String.Escape
  .sh    { color: $oceanic-08 } // Literal.String.Heredoc
  .si    { color: $oceanic-08 } // Literal.String.Interpol
  .sx    { color: $oceanic-08 } // Literal.String.Other
  .sr    { color: $oceanic-09 } // Literal.String.Regex
  .s1    { color: $oceanic-08 } // Literal.String.Single
  .ss    { color: $oceanic-08 } // Literal.String.Symbol
  .bp    { color: $oceanic-06 } // Name.Builtin.Pseudo
  .vc    { color: $oceanic-12 } // Name.Variable.Class
  .vg    { color: $oceanic-09 } // Name.Variable.Global
  .vi    { color: $oceanic-12 } // Name.Variable.Instance
  .il    { color: $oceanic-14 } // Literal.Number.Integer.Long
}
