
.dark-bg {
	background-color: $background-color-dark;
}

.gradient-bg {
	background-image: linear-gradient(-239deg, #0b0c10 10%, #311943, #0e4459);
}

.light-bg {
	background-color: $background-color-light;
}

.light-text {
	color: $text-color-light;

	h1, h2, h3, h4, h5, h6 {
		text-shadow: 0px 0px 12px #3abae16e;
	}
}

.dark-text {
	color: $text-color-dark;
}

ul.no-bullet{
	margin:0;
	padding:0;

	li {
		list-style:none;
	}
}

.breakout {
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}

.big-vertical-fit {
	height: 80vh;
}

.big-hero {
	height: 60vh;
}

.big-trailer {
	max-width: 960px;
}

.hero {
	background-image: url($baseurl + "/images/mixolumia-gem-logo-color.svg");
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center;
	margin-top:-40px;
	margin-bottom: 24px;
}

.centered {
	text-align: center;
}

.bottom-padding {
	padding-bottom: 16px;
}

.feature-buttons {
  display: block;
  max-width: 420px;
  margin: 0 auto;

  a, a:visited {
  	display: block;
    flex-grow: 1;
    margin: 16px;
    padding: 6px 32px 8px;
    text-align: center;
    font-weight: 900;
    font-size: 24px;
    color: $text-color-light;
    background: linear-gradient(-24deg, #13273d, #2d1943);
    clip-path: $gem-path;
    letter-spacing: 1px;
    text-shadow: 0px 0px 12px #3abae16e;

    &:hover {
      background: linear-gradient(-24deg, $brand-color-dark, $brand-color);
      color: $text-color-dark;
    }
  }
}

.special-buttons {
  display: flex;
  margin: 0 -16px;
  flex-wrap: wrap;

	a, a:visited {
	    flex-grow: 1;
	    flex-basis: 0;
	    margin: 16px;
	    padding: 6px 32px 8px;
	    text-align: center;
	    font-weight: 900;
	    font-size: 24px;
	    letter-spacing: 1px;
	    color: $text-color-light;
	    text-shadow: 0px 0px 12px #3abae16e;
	    box-shadow: 0px 0px 12px #3abae16e;
	    border: 1px solid $brand-color-dark;
	    position:relative;

	    &:hover {
	    	background: linear-gradient(-24deg, $brand-color-dark, $brand-color);
    		color: $text-color-dark;
	    }
    }
}