@charset "utf-8";

// Define defaults for each variable.

$base-font-family: "Lato", "HelveticaNeue", "Helvetica Neue", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif !default;
$base-font-size:   18px !default;
$base-font-weight: 400 !default;
$small-font-size:  $base-font-size * 0.875 !default;
$base-line-height: 1.5 !default;

$spacing-unit:     32px !default;

$text-color-light:      	#e9edf1 !default;
$text-color-light-2:		#7a7e91 !default;
$text-color-dark:			#0b0c10 !default;
$background-color-light: 	#e9edf1 !default;
$background-color-dark: 	#0b0c10 !default;
$brand-color:      			#57DEFF !default;
$brand-color-dark: 			#3aa8e8 !default;
$brand-color-dark-2:		#3052f9 !default;

$itch-color:				#fa5c5c !default;
$steam-color:				#00adee !default;

$grey-color:       #1F2833 !default;
$grey-color-light: lighten($grey-color, 40%) !default;
$grey-color-dark:  darken($grey-color, 25%) !default;

$table-text-align: left !default;

// Width of the content area
$content-width:    640px !default;

$on-palm:          600px !default;
$on-laptop:        640px !default;

$nav-min:		   1000px !default;

$gem-path: polygon(90% 100%,100% 50%,90% 0%,10% 0%,0% 50%,10% 100%);

// Use media queries like this:
// @include media-query($on-palm) {
//   .wrapper {
//     padding-right: $spacing-unit / 2;
//     padding-left: $spacing-unit / 2;
//   }
// }
@mixin media-query($device) {
  @media screen and (max-width: $device) {
    @content;
  }
}

@mixin relative-font-size($ratio) {
  font-size: $base-font-size * $ratio;
}

// Import partials.
@import
  "mixo/base",
  "mixo/layout",
  "mixo/utility",
  "mixo/syntax-highlighting"
;